
	import {defineComponent,ref,toRefs,reactive,onMounted,watch,computed} from 'vue';
	import {useStore} from 'vuex'
	import {useRoute,useRouter} from 'vue-router'
	import { ElMessage } from 'element-plus'
	export default defineComponent({
		name: 'Order',
		components: {},
		setup(props, context) {
			onMounted(()=>{
				console.log(useStore());
			});
			const store = useStore();
			const router = useRouter();
			let orderLists = store.getters.getOrderLists;
			const gohome = ()=>{
				router.push({name:"Homec",params:{}});;
			}
			function order(){
				function cancelOrder(i:number){
					store.dispatch('cmCancelOrder', {index: i});
				}
				function sureOrder(i:number){
					store.dispatch('cmSureOrder',{index:i});
					ElMessage({
					    message: '确认收货!',
					    type: 'success',
					});
				}
				let total = computed(()=>{
					let sum = 0;
					orderLists.forEach(function(val:any){
						sum +=val.price*val.nums; 
					});
					return sum;
				});
				
				return {cancelOrder,sureOrder,total,orderLists};
			}
			return {...order(),gohome};
		}
	});
	
